<template lang="pug">
.stage-container
  .dragon
  v-layout(row)
    v-flex(md6)
    v-flex(md5)
      h1 {{ genderName }} {{ user.name }},  !
      p     .
      p     ,   .
        |      !
      v-btn.ml-0(@click="next" color="primary" depressed) 
</template>


<script>
import { mapGetters } from 'vuex';
import { GENDER } from '@/constants';


export default {
  props: {
    next: { type: Function, default: () => {} },
  },
  computed: {
    ...mapGetters({
      user: 'getUser',
    }),
    genderName() {
      return this.user.gender === GENDER.male ? '' : '';
    },
  },
};
</script>
